import React from 'react'
import aboutDoctor from '../../../assets/images/aboutDoctor.png'

const AboutDoctors = () => {
  return (
    <>
      <div className="container mt-100 mt-60">
        <div className="row align-items-center">
          <div className="col-lg-7 col-md-6">
            <div className="section-title me-lg-5">
              <span className="badge rounded-pill bg-soft-primary">About Doctris</span>
              <h4 className="title mt-3 mb-4">
                Good Services And Better <br /> Health By Our Specialists
              </h4>
              <p className="para-desc text-muted">
                Great doctor if you need your family member to get effective immediate assistance,
                emergency treatment or a simple consultation.
              </p>
              <p className="para-desc text-muted">
                The most well-known dummy text is the &apos;Lorem Ipsum&apos;, which is said to have
                originated in the 16th century. Lorem Ipsum is composed in a pseudo-Latin language
                which more or less corresponds to &apos;proper&apos; Latin. It contains a series of
                real Latin words.
              </p>
              <div className="mt-4">
                <a href="aboutus.html" className="btn btn-soft-primary">
                  Read More
                </a>
              </div>
            </div>
          </div>
          <div className="col-lg-5 col-md-6 mt-4 pt-2 mt-sm-0 pt-sm-0">
            <div className="position-relative">
              <img src={aboutDoctor} className="img-fluid" alt="" />
            </div>
          </div>
        </div>
      </div>
    </>
  )
}

export default AboutDoctors
