import axios from 'axios'
import PropTypes from 'prop-types'
import React, { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { useNavigate } from 'react-router-dom'
import { setUser } from '../features/userSlice'
import { setLoggedInRoleUserData } from '../features/LoggedInRoleUserDataSlice'
import { Container, Spinner } from 'react-bootstrap'

const ProtectedRoutes = ({ children }) => {
  const user = useSelector((state) => state.user.user)
  const loggedInRoleUser = useSelector((state) => state.loggedInRoleUserData.loggedInRoleUser)
  const navigate = useNavigate()
  const dispatch = useDispatch()
  const [loading, setLoading] = useState(true)

  const getUser = async () => {
    try {
      const res = await axios.post(
        '/api/v1/user/getUser',
        { token: localStorage.getItem('token') },
        { headers: { Authorization: `Bearer ${localStorage.getItem('token')}` } },
      )
      if (res.data.success) {
        dispatch(setUser(res.data.data.user))
        if (res.data.data.user.role === 'admin') {
          dispatch(setLoggedInRoleUserData(res.data.data.admin))
          setLoading(false)
          navigate('/admin')
        }
        if (res.data.data.user.role === 'doctor') {
          console.log('dispatching doctor data')
          dispatch(setLoggedInRoleUserData(res.data.data.doctor))
          setLoading(false)
          navigate('/doctor')
        }
        if (res.data.data.user.role === 'receptionist') {
          dispatch(setLoggedInRoleUserData(res.data.data.receptionist))
          setLoading(false)
          navigate('/receptionist')
        }
        if (res.data.data.user.role === 'patient') {
          dispatch(setLoggedInRoleUserData(res.data.data.patient))
          setLoading(false)
          navigate('/patient')
        }
      }
    } catch (error) {
      console.log(error)
      console.log('clearing token')
      setLoading(false)
      localStorage.removeItem('token')
      navigate('/login')
    }
    finally {
      setLoading(false)
    }
  }

  useEffect(() => {
    if (!user) {
      getUser()
    }
  }, [user, loggedInRoleUser]) //
  return (
    <>
      {loading ? (
        <>
          <Container
            fluid
            className="d-flex justify-content-center align-items-center"
            style={{
              height: '100vh',
            }}
          >
            <Spinner animation="border" role="status">
              <span className="visually-hidden">Loading...</span>
            </Spinner>
          </Container>
        </>
      ) : (
         <>{children}</>
      )}
    </>
  )
}
export default ProtectedRoutes

ProtectedRoutes.propTypes = {
  children: PropTypes.node.isRequired,
}
