import React, { Component, Suspense } from 'react'
import { BrowserRouter, Route, Routes } from 'react-router-dom'
import './scss/style.scss'
import './style.css'

// import Login from './pages/Login'
import Home from '../src/views/pages/Home'
import AboutUs from '../src/views/pages/AboutUs'
import Department from '../src/views/pages/Department'
import Contact from '../src/views/pages/Contact'
import PrivacyPolicy from '../src/views/pages/PrivacyPolicy'
import ProtectedRoutes from './components/ProtectedRoutes'

const loading = (
  <div className="pt-3 text-center">
    <div className="sk-spinner sk-spinner-pulse"></div>
  </div>
)

// Containers
const DefaultLayout = React.lazy(() => import('./layout/DefaultLayout'))

// Pages
const Login = React.lazy(() => import('../src/views/pages/Login'))
const Register = React.lazy(() => import('./views/pages/Register'))
const Page404 = React.lazy(() => import('./views/pages/page404/Page404'))
const Page500 = React.lazy(() => import('./views/pages/page500/Page500'))

class App extends Component {
  render() {
    return (
      <BrowserRouter>
        <Suspense fallback={loading}>
          <Routes>
            <Route path="/" element=<Home /> />
            <Route path="/aboutus" element=<AboutUs /> />
            <Route path="/register" element={<Register />} />
            <Route path="/department" element=<Department /> />
            <Route path="/contact" element=<Contact /> />
            <Route path="/privacy" element=<PrivacyPolicy /> />
            <Route exact path="/login" name="Login Page" element={<Login />} />
            <Route exact path="/register" name="Register Page" element={<Register />} />
            <Route exact path="/404" name="Page 404" element={<Page404 />} />
            <Route exact path="/500" name="Page 500" element={<Page500 />} />
            <Route path="*" name="Home" element={<DefaultLayout />} />
          </Routes>
        </Suspense>
      </BrowserRouter>
    )
  }
}

export default App
