import { createSlice } from '@reduxjs/toolkit'

export const loggedInRoleUserData = createSlice({
  name: 'loggedInRoleUserData',
  initialState: {
    loggedInRoleUserData: null,
  },
  reducers: {
    setLoggedInRoleUserData: (state, action) => {
      state.loggedInRoleUserData = action.payload
    },
    clearLoggedInRoleUserData: (state) => {
      state.loggedInRoleUserData = null
    },
  },
})

export const { setLoggedInRoleUserData,clearLoggedInRoleUserData } = loggedInRoleUserData.actions
