import React from 'react'
import PrivacyPolicyHero from '../Website/PrivacyPolicy/PrivacyPolicyHero'
import PrivacyPolicyOverView from '../Website/PrivacyPolicy/PrivacyPolicyOverView'
import NavBar from '../Website/NavBar';
import Footer from '../Website/Footer';

const PrivacyPolicy = () => {
  return (
    <>
      <NavBar />
      <PrivacyPolicyHero />
      <PrivacyPolicyOverView />
      <Footer />
    </>
  )
}

export default PrivacyPolicy