import React from 'react'
import { Link } from 'react-router-dom'

const NavBar = () => {


  return (
    <>
      <header id="topnav" className="navigation">
        <div className="container">
          {/* Logo container*/}
          <div>
            <Link className="logo" to="/">
              <span className="logo-light-mode">
                <h4 className="text-light">Aman</h4>
              </span>
              <img src="../assets/images/logo-light.png" height={22} className="logo-dark-mode" alt="" />
            </Link>
          </div>
          {/* End Logo container*/}
          {/* Start Mobile Toggle */}
          <div className="menu-extras">
            <div className="menu-item">
              {/* Mobile menu toggle*/}
              <Link className="navbar-toggle" id="isToggle" onClick="toggleMenu()">
                <div className="lines">
                  <span />
                  <span />
                  <span />
                </div>
              </Link>
              {/* End mobile menu toggle*/}
            </div>
          </div>
          {/* End Mobile Toggle */}
          {/* Start Dropdown */}
          <ul className="dropdowns list-inline mb-0">
            <li className="list-inline-item mb-0 ms-1">
              <div className="dropdown dropdown-primary">

                <Link to="/patient" className="btn btn-primary">Patietnt Portal</Link>

              </div>
            </li>
          </ul>
          {/* Profile click Dropdown */}
          <div id="navigation">
            {/* Navigation Menu*/}
            <ul className="navigation-menu nav-left nav-light">
              <li>
                <Link to="/">Home</Link>
              </li>
              <li>
                <Link to="/department">Departments</Link><span className="menu-arrow" />
              </li>
              <li>
                <Link to="/contact">Contact</Link>
              </li>
              <li>
                <Link to="/aboutus">About Us</Link>
              </li>
              <li>
                <Link to="/privacy">Privacy Policy</Link>
              </li>
            </ul>{/*end navigation menu*/}
          </div>{/*end navigation*/}
        </div>{/*end container*/}
      </header>
    </>
  )
}

export default NavBar