import React from 'react'
// import mainHeroBg from '../../assets/mainHeroBg.jpg';
import mainHeroBg from '../../../assets/images/mainHeroBg.jpg'

const MainHero = () => {
  const mystyle = {
    // backgroundImage: `url(${mainHeroBg})`,
    backgroundRepeat: 'no-repeat',
    backgroundImage: "linear-gradient(rgba(0,0,0,0.5),rgba(0,0,0,0.5)),url(" + mainHeroBg + ")",
  }
  return (
    <>
      <section className="bg-half-260 d-table w-100" style={mystyle}>
        <div className="bg-overlay bg-overlay-dark"></div>
        <div className="container">
          <div className="row mt-5 mt-lg-0">
            <div className="col-12">
              <div className="heading-title">
                {/* <img src="../assets/images/logo-icon.png" height="50" alt="" /> */}
                <h4 className="display-4 fw-bold text-white title-dark mt-3 mb-4">
                  Meet The <br /> Best Doctor
                </h4>
                <p className="para-desc text-white-50 mb-0">
                  Great doctor if you need your family member to get effective immediate assistance,
                  emergency treatment or a simple consultation.
                </p>
              </div>
            </div>
          </div>
        </div>
      </section>
    </>
  )
}

export default MainHero
