import { Icon } from '@iconify/react'
import React from 'react'
import { Link } from 'react-router-dom'

const PhoneEmailLocation = () => {
    return (
        <>
            <section className="mt-100 mt-60">
                <div className="container">
                    <div className="row">
                        <div className="col-lg-4 col-md-6">
                            <div className="card features feature-primary text-center border-0">
                                <div className="icon text-center rounded-md mx-auto">
                            <Icon icon="uil:phone" className="h3 mb-0 text-primary" />

                                </div>
                                <div className="card-body p-0 mt-3">
                                    <h5>Phone</h5>
                                    <p className="text-muted mt-3">Great doctor if you need your family member to get effective
                                        assistance</p>
                                    <a href="tel:+152534-468-854" className="link">+152 534-468-854</a>
                                </div>
                            </div>
                        </div>
                        <div className="col-lg-4 col-md-6 mt-4 mt-sm-0 pt-2 pt-sm-0">
                            <div className="card features feature-primary text-center border-0">
                                <div className="icon text-center rounded-md mx-auto">
                            <Icon icon="uil:envelope" className="h3 mb-0 text-primary" />

                                </div>
                                <div className="card-body p-0 mt-3">
                                    <h5>Email</h5>
                                    <p className="text-muted mt-3">Great doctor if you need your family member to get effective
                                        assistance</p>
                                    <a href="mailto:contact@example.com" className="link">contact@example.com</a>
                                </div>
                            </div>
                        </div>
                        <div className="col-lg-4 col-md-6 mt-4 mt-lg-0 pt-2 pt-lg-0">
                            <div className="card features feature-primary text-center border-0">
                                <div className="icon text-center rounded-md mx-auto">
                            <Icon icon="uil:map-marker" className="h3 mb-0 text-primary" />
                                </div>
                                <div className="card-body p-0 mt-3">
                                    <h5>Location</h5>
                                    <p className="text-muted mt-3">C/54 Northwest Freeway, Suite 558, <br />Houston, USA 485</p>
                                    <Link to="#" className="link">View on Google map</Link>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                
            </section>

        </>
    )
}

export default PhoneEmailLocation