import { createSlice } from '@reduxjs/toolkit'

export const bpAndW = createSlice({
  name: 'bpAndW',
  initialState: {
    bpAndW: null,
  },
  reducers: {
    setBPAndW: (state, action) => {
      state.bpAndW = action.payload
    },
    clearLoggedInRoleUserData: (state) => {
      state.bpAndW = null
    },
  },
})

export const { setBPAndW, clearLoggedInRoleUserData } = bpAndW.actions
